.form {
  position: relative;
  max-width: 51.4%;
  &__btn {
    margin-bottom: 16px;
    padding: 18px 48px;
    text-align: center;
    font: $form-btn;
    border-radius: 12px;
    background-color: $blue;
    color: $white;
    transition: background-color 0.15s ease-in-out;

    &:hover {
      background-color: $black;
      transition: background-color 0.15s ease-in-out;
    }
  }
}

.payment-switch {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  align-items: center;
}

.label-checkbox .input-checkbox {
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 24px;
  height: 14px;
  border-radius: 80px;
  vertical-align: top;
  background: $checkbox-back;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}
.label-checkbox .input-checkbox:hover {
  background: $checkbox-hover;
}
.label-checkbox .input-checkbox:before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  transition: all 0.15s ease-in-out;
}
.label-checkbox input[type=checkbox]:checked + .input-checkbox {
  background: $blue;
  transition: all 0.15s ease-in-out;
}
.label-checkbox input[type=checkbox]:checked + .input-checkbox:hover {
  background: $black;
  transition: all 0.15s ease-in-out;
}
.label-checkbox input[type=checkbox]:checked + .input-checkbox:before {
  transform: translateX(10px);
  transition: all 0.15s ease-in-out;
}
.label-checkbox input[type=checkbox]:not(:disabled) + .input-checkbox {
  cursor: pointer;
}

.payment {
  margin-bottom: 32px;
  transition: all 0.15s ease-in-out;

  &__title {
    margin-bottom: $margin-form-title;
    font:$subtitle;
  }

  &__checkbox {
    max-height: 14px;
  }

}

.checkbox-title {
  margin-right: 10px;
  font: 400 12px/16px $Graphik;
}

.cards {
  display: flex;
  flex-wrap: wrap;

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 8px;
    padding: 15px;
    width: 100%;
    max-width: 48.8%;
    font: $input;
    border: 1px solid $border;
    border-radius: 12px;
    cursor: pointer;
    transition: border-color 0.15s ease-in-out;

    &:first-child {
      border: 2px solid $blue;
    }

    &:nth-child(2n+1) {
      margin-right: 8px;
    }

    &:hover {
      border-color: $blue;
      transition: border-color 0.15s ease-in-out;
    }
  }

  &__descr {
    display: block;
    margin-bottom: 16px;
    font: $card-descr;
  }
}

.logo-group {
  max-height: 16px;
}

.callback {
  &__title {
    margin-bottom: $margin-form-title;
    font: $subtitle;
  }

  &__block {
    margin-bottom: $margin-form-title;
  }

  &__item {
    position: relative;
    display: block;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
}

.input {
  padding: 18px 50px 18px 18px;
  min-height: 56px;
  background-color: $input-back;
  border: 1px solid $border;
  border-radius: 10px;
  transition: border-color 0.15s ease-in-out, background-color 0.15s ease-in-out;

  &::placeholder {
    color: $black;
  }

  &:hover {
    border-color: $border-hover;
    transition: border-color 0.15s ease-in-out;

  }

  &:focus {
    padding-bottom: 10px;
    outline-color: $border-hover;
    background-color: $white;
    transition: all 0.15s ease-in-out;
  }

  &:not(:placeholder-shown) {
    padding-bottom: 10px;
    transition: all 0.15s ease-in-out;
  }
}

.label-name {
  position: absolute;
  top: 18px;
  left: 18px;
  transition: all 0.15s ease-in-out;
}

.input:focus + .label-name, .input:not(:placeholder-shown) + .label-name{
  top: 10px;
  font-size: 11px;
  color: #6B6B6B;
  transition: all 0.15s ease-in-out;
}

.privacy {
  width: 97%;
  font: $small-descr;
  color: #6B6B6B;
}

.input--invalid {
  border-color: $border-error;
  background-color: $back-error;
}

.error {
  position: relative;
  top: 4px;
  font-size: 11px;
  line-height: 12px;
  color: $border-error;
}

@media (max-width: 1023px) {
  .form {
    max-width: 100%;

    &__btn {
      width: 100%;
    }
  }

  .payment {
    margin-bottom: 36px;

    &__title {
      margin-bottom: 20px;
      font: $subtitle-md;
    }
  }

  .cards {

    &__descr {
      margin-bottom: 12px;
      font: $small-descr;
    }

    &__item {
      margin-bottom: 4px;
      padding: 11px;
      max-width: 49.5%;

      &:nth-child(2n+1) {
        margin-right: 4px;
      }
    }
  }

  .consult {
    width: 98%;
  }

  .callback {
    &__title {
      margin-bottom: 20px;
      font: $subtitle-md;
    }
  }
}
@media (max-width: 767px) {
  .cards {
    &__item {
      max-width: 49.2%;
      padding-right: 10px;
    }
  }

  .payment-switch {
    top: 15px;
    right: 15px;
  }
}

@media (max-width: 359px) {
  .privacy {
    width: 100%;
  }
}

